







































import { Component, Vue } from 'vue-property-decorator';
import {
  Mutation
} from 'vuex-class';

@Component
export default class Onboarding extends Vue {
  @Mutation acquireOnboarding;
  private page = 0;
  private mounted () {
    this.page = 0;
    this.acquireOnboarding();
  }
  private nextPage () {
    this.page ++;
    if (this.page === 2) {
      this.goHome();
    }
  }
  private goHome () {
    this.$router.push({name: 'home'});
  }

}
